}

.center{
text-align:center;
}

a:hover,a:active,a:focus {
outline: 0;
}

:focus {outline:none;}

::-moz-focus-inner {border:0;}

.form{
margin:10px 0px;
}


.well{
padding:5px 10px;
background:#eee url('../img/header-back.png') repeat;
