/* Header */

header{
padding:25px 0px 0px 0px;
border-bottom:1px solid #ddd;
background:url('../img/header-back.png') repeat;
}

header h1 a, header h1 a:visited, header h1 a:hover{
color:#777;
text-decoration:none;
font-size:45px;
line-height:55px;
}

header span{
font-family:Georgia, "Times New Roman", serif;
}

header .list{
position:relative;
top:-25px;
width:230px;
background:#fff;
padding:7px 10px;
border:1px solid #ddd;
box-shadow:0px 1px 2px #ddd;
border-top:none;
border-bottom-left-radius:10px;
border-bottom-right-radius:10px;
font-size:12px;
line-height:20px;
color:#777;
}

header .list hr{
margin:2px 0px 6px 0px;
}

/* Navigation */

.navbar{
margin:0px 0px 15px 0px;
font-family:Tahoma, Verdana, Arial, Helvetica, sans-serif;
}

.navbar > .container{
max-width:1000px;
}

.navbar{
border-radius:0px;
background-image: none;
background-repeat: no-repeat;
filter: none !important;
border-left:0px !important;
border-right:0px !important;
}

.navbar-collapse{
	padding-left:0px;
}

.navbar button{
font-size:11px;
padding:8px 10px;
color:#fff !important;
text-shadow:none !important;
}

.navbar button:hover{
	color: #fff !important;
} 

.navbar-toggle .icon-bar {
	background:#fff;
}

.navbar .caret{
border-top-color:#fff !important;
border-bottom-color:#fff !important;
}

.navbar .brand{
color:#fff !important;
text-shadow:none !important;
}

.navbar .nav > li > a{
text-shadow:none !important;
padding:14px 15px 14px;
color:#fff !important;
-webkit-transition:background 1s ease;
-moz-transition:background 1s ease;
-o-transition:background 1s ease;
transition:background 1s ease;
font-size:13px !important;
}

.navbar .nav > li > a:hover{
color:#fff !important;
-webkit-transition:background 1s ease;
-moz-transition:background 1s ease;
-o-transition:background 1s ease;
transition:background 1s ease;
}

.navbar .nav .active > a,
.navbar .nav .active > a:hover,
.navbar .nav .active > a:focus {
color: #ffffff;
box-shadow:none;
}


.nav-collapse a{
font-weight:normal !important;
text-shadow:none !important;
}

.dropdown-menu{
border-radius:0px !important;
box-shadow:none !important;
border-bottom:none !important;
padding:0px 0px;
margin:0px;
text-shadow:none !important;
z-index: 2000;
}

.dropdown-menu a{
padding:8px 10px !important;
color:#fff !important;
-webkit-transition:background 1s ease;
-moz-transition:background 1s ease;
-o-transition:background 1s ease;
transition:background 1s ease;
font-size:13px !important;
text-shadow:none !important;
}

.dropdown-menu a:hover,.dropdown-menu a:focus{
filter:none !important;
-webkit-transition:background 1s ease;
-moz-transition:background 1s ease;
-o-transition:background 1s ease;
transition:background 1s ease;
}

.dropdown-menu::after, .dropdown-menu::before{
border:none !important;
}

/* Carousel */

.left.carousel-control {
	position: absolute;
	top: 50%;
	left: 15px;
	width: 50px;
	height: 50px;
	margin-top: -30px;
	font-size: 50px;
	font-weight: 100;
	line-height: 50px;
	color: #ffffff;
	text-align: center;
	background: #222222;
	border: 3px solid #ffffff;
	-webkit-border-radius: 23px;
	-moz-border-radius: 23px;
	border-radius: 30px;
	opacity: 0.5;
	filter: alpha(opacity=50);
}

.right.carousel-control{
	position: absolute;
	top: 50%;
	left:auto;
	right:15px;
	width: 50px;
	height: 50px;
	margin-top: -30px;
	font-size: 50px;
	font-weight: 100;
	line-height: 50px;
	color: #ffffff;
	text-align: center;
	background: #222222;
	border: 3px solid #ffffff;
	-webkit-border-radius: 23px;
	-moz-border-radius: 23px;
	border-radius: 30px;
	opacity: 0.5;
	filter: alpha(opacity=50);
}

.carousel-control .icon-next {
	width: 30px;
	height: 30px;
	margin-top: -17px;
	margin-left: -15px;
	font-size: 30px;
	line-height: 30px;
}

.carousel-control .icon-prev {
	width: 30px;
	height: 30px;
	margin-top: -17px;
	margin-left: -15px;
	font-size: 30px;
	line-height: 30px;
}


/* Social */

.social{
font-size:16px;
margin-bottom:10px;
}

.social a, .social a:visited, .social a:hover{
color:#fff;
text-decoration:none;
}

.social i{
display:inline-block;
height:30px;
width:30px;
text-align:center;
line-height:30px;
-webkit-transition:background 1s ease;
-moz-transition:background 1s ease;
-o-transition:background 1s ease;
transition:background 1s ease;
border-radius:30px;
}

.social i:hover{
-webkit-transition:background 1s ease;
-moz-transition:background 1s ease;
-o-transition:background 1s ease;
transition:background 1s ease;
}

/* Content */

.content ul{
list-style-type: none !important;
padding: 0px;
margin: 0px;
}

.content ul li{
background-repeat: no-repeat;
background-position: 2px 7px; 
background-size:13px 13px;
padding:4px 0px;
padding-left: 21px; 
}

.flexslider  ul li{
background-image:none;
padding:0px;
}

.flexslider{
margin-top:10px;
margin-bottom:15px;
border:1px solid #bbb;
padding:1px;
border-radius:0px;
box-shadow:none;
}

.flexslider .flex-control-nav{
bottom:0px;
}

.flex-control-paging li a{
background:#fff;
