}

.flexslider .slides > li {
position:relative;
}

.flex-caption{
	width: 40%;
	padding: 10px;
	margin: 0 0 40px 40px;
	background-color: #222;
	background: rgba(0, 0, 0, 0.8);
	position: absolute;
	display: block;
	left: 0;
	bottom: 0;
}

.flex-caption h3 {
	color: #fff;
	font-size: 16px;
	padding-bottom: 5px;
	margin:0;
   margin-bottom:3px;
	line-height: 22px;
}

.flex-caption h3 span{
   padding-bottom:3px;
   border-bottom:1px solid #444;
}
	
.flex-caption p {
	margin:0;
	line-height: 19px;
	color: #ddd;
}

